@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Concert+One&display=swap);
:root {
  --primary: #12232e;
  --secondary: #073652;
  --third: #4da8da;
  --w-text: #f6fbfb;
  --mobile-p: 1.1rem;
  --pad-p: 1.9rem;
  --desktop-p: 1.8rem;
  --mobile-title: 2.2rem;
  --pad-title: 3.3rem;
  --desktop-title: 3.3rem;
}

body {
  background-color: #f6fbfb;
  background-color: var(--w-text);
  font-family: 'Concert One', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.top-container {
  margin: 50px 0;
  width: 100vw;
  text-align: center;
}

.btn-top {
  transform: scale(2);
  color: #073652;
  color: var(--secondary);
  background-color: #12232e;
  background-color: var(--primary);
  border-radius: 50%;
  box-shadow: -1px 2px 3px -1px #000000;
}

.btn-top:hover {
  cursor: pointer;
  -webkit-animation: 1s ease-in-out top infinite;
          animation: 1s ease-in-out top infinite;
}

@-webkit-keyframes top {
  0% {
    transform: translateY(0px) scale(2);
  }

  50% {
    transform: translateY(-5px) scale(2.2);
  }

  100% {
    transform: translateY(0px) scale(2);
  }
}

@keyframes top {
  0% {
    transform: translateY(0px) scale(2);
  }

  50% {
    transform: translateY(-5px) scale(2.2);
  }

  100% {
    transform: translateY(0px) scale(2);
  }
}

.home-text-container {
  height: 35%;
  width: 85%;
  margin-top: 120px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  text-shadow: -1px 1px 1px var(--primary);
  color: var(--secondary);
  z-index: 6;
}

.home-text-container h1 {
  margin: 0;
  font-size: 2.5rem;
  display: inline;
}

.flip {
  color: var(--third);
  -webkit-animation: 1s ease-in-out flip-in;
          animation: 1s ease-in-out flip-in;
  margin-bottom: 20px;
}

.home-text-container p {
  margin: 0;
  font-size: 1.5rem;
  margin-bottom: 5px;
}

.icons {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 20%;
  width: 90%;
}

.icons-animation {
  text-decoration: none;
  color: var(--secondary);
  font-size: 0.8rem;
  text-shadow: -1px 1px 1px var(--primary);
  -webkit-animation: 1.3s ease-in-out flip;
          animation: 1.3s ease-in-out flip;
}

@-webkit-keyframes flip-in {
  from {
    transform: rotateX(90deg);
    opacity: 0;
  }
}

@keyframes flip-in {
  from {
    transform: rotateX(90deg);
    opacity: 0;
  }
}

@-webkit-keyframes flip {
  from {
    transform: translateX(350px) rotate(360deg) scale(1.2);
    opacity: 0;
  }
}

@keyframes flip {
  from {
    transform: translateX(350px) rotate(360deg) scale(1.2);
    opacity: 0;
  }
}

@media screen and (min-width: 760px) {
  .home-text-container {
    width: 75%;
    height: 30%;
    margin-top: 200px;
  }

  .flip {
    margin-bottom: 35px;
  }

  .home-text-container h1 {
    font-size: 3.5rem;
  }

  .home-text-container p {
    font-size: 2rem;
    margin-bottom: 35px;
  }

  .icons {
    height: 20%;
    width: 60%;
  }

  .icons-animation {
    font-size: 1rem;
  }
}

@media screen and (min-width: 992px) {
