.card {
  height: 60vh;
  width: 75%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10%;
  background-image: url(/Meiry-Portfolio/static/media/blob.13180e2e.svg);
  background-size: 280%;
  background-repeat: no-repeat;
  background-position-y: 150px;
  background-position-x: -100px;
  text-shadow: -1px 2px 3px var(--primary);
  position: relative;
  margin-top: 150px;
}

.card-animation {
  -webkit-animation: 0.7s ease-in-out fade-in;
          animation: 0.7s ease-in-out fade-in;
}

.unseen {
  opacity: 0;
}

.svg-char {
  width: 20%;
  height: 10%;
  position: absolute;
  bottom: -25px;
  right: 10px;
  z-index: 1000;
  transform: scale(2);
}

.project-image {
  position: absolute;
  top: -35px;
  left: -20px;
  height: 13%;
  width: 35%;
  border-radius: inherit;
  box-shadow: -1px 2px 3px -2px #000000;
}

.card-content {
  position: absolute;
  top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  line-height: 1.1rem;
  color: var(--w-text);
  margin-top: 10px;
  height: 90%;
  width: 70%;
}

.project-links {
  width: 100%;
