  text-align: center;
  font-size: 1.6rem;
  font-weight: 600;
  margin: 0;
}

.card-content h4 {
  font-size: 1.1em;
  font-weight: 500;
  margin: 0;
}

.card-content ul {
  list-style-type: none;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  margin: 0;
  padding: 0;
}

.card-content li {
  font-size: 0.9em;
  font-weight: 300;
  margin: 10px 0;
}

.project-links a:hover {
  color: #d4d4d4;
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(150px) scale(0.9);
